<template>
  <div>
    <div class="reply" v-for="(reply,indexq) in comment.reply" v-bind:key="indexq">
      <b>{{reply.discussantName}}&nbsp;&nbsp;回复&nbsp;&nbsp;{{comment.discussantName}}<span>{{reply.commentTime}}</span></b>
      <span v-if="reply.stepName" style="color: #6612cf">#{{reply.stepName}}</span>
      <span v-if="reply.materialName" style="color: #6612cf">#{{reply.materialName}}</span>
      <p v-html="reply.content" @click="changeComment(reply.id, reply.discussantName)">
        {{reply.content}}</p>
      <list :comment="reply" @changeCommenter='changeComment'></list>
    </div>
  </div>
</template>
<script>
export default {
  name: 'List',
  props: {
    comment: {}
  },
  methods: {
    changeComment (id, name) {
      this.$emit('changeCommenter', id, name)
    }
  }
}
</script>
