<template>
  <div id="app" style="background-color:#f8f8f8;">
    <!--    上方导航栏-->
    <div class='container-fluid' style="padding: 0px 0px 0px 0px">
      <Header :noticeIs='noticeIs' img="" school='Go语言实战抽奖系统'></Header>
      <div class='tap'>
        <HorizontalTap class='center' :data='data2' :active="0" style="padding-left: 100px;"></HorizontalTap>
      </div>
    </div>
    <!--  左侧导航栏-->
    <div class="container1 col-md-offset-1 col-md-2" style="margin-top: 40px">
      <div class="fontStep">开发阶段和任务</div>
      <transition name="fade">
        <TeamStepList v-if="show" @activeItem="activeItemList" :data="stepData" :expand-id=currentStageId
                      :active-id=currentStepId></TeamStepList>
      </transition>
    </div>
    <!--  上方项目介绍-->
    <div class="container1 col-md-8" style="margin-top: 40px;margin-left: 15px;margin-right: 15px">
      <div style="height: 300px;">
        <div class="container-fluid">
          <div class="left col-lg-5" style="width: 40%">
            <img style="height: 300px;width: 100%;" src="../../assets/img/banner1.png">
          </div>
          <div class="left col-lg-5" style="width: 50%" v-if="stepInfo && stepInfo.category === 0">
            <p class="xmname">{{projectName}}</p>
            <!--            <p class="xmstep" v-show="stepInfo"> 当前阶段任务：{{stepInfo.stepName}}</p>-->
            <div style="border:1px dashed rgba(182, 195, 214, .6);min-height: 250px;">
              <div class="stepInfoContent" style="    margin-top: 10px;">
<!--                <i class="iconfont iconrenwumiaoshu mr-2"></i>-->
                <h4 class="xm-left-column-label"><span></span>任务描述：</h4>
<!--                <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color:inherit">任务描述：</span>-->
                <span style="color:#666666" v-show="stepInfo.description">
              {{stepInfo.description}}
            </span>
              </div>
              <div class="stepInfoContent">
<!--                <i class="iconfont iconrenwumingcheng mr-2"></i>-->
<!--                <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">任务目的：</span>-->
                <h4 class="xm-left-column-label"><span></span>任务目的：</h4>
                <span style="color:#666666" v-show="stepInfo.purpose">
              {{stepInfo.purpose}}
            </span>
              </div>
              <div class="stepInfoContent">
<!--                <i class="iconfont iconrenwuyaoqiu mr-2"></i>-->
<!--                <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">任务要求：</span>-->
                <h4 class="xm-left-column-label"><span></span>任务要求：</h4>
                <span style="color:#666666" v-show="stepInfo.required">
              {{stepInfo.required}}
                <span v-show="templateName[0]!==''">请根据
                  <a v-for="(obj,index) in templateName" v-bind:key="index" class="ml-2"
                     style="color:#6715CE;cursor:pointer;" @click="downloadTemplate(obj)">{{obj.split('$_$')[1]}}</a>上传成果物
                </span>
                </span>
              </div>
            </div>
          </div>
          <div class="left col-lg-5" style="width: 50%;text-align: center;" v-if="stepInfo && stepInfo.category === 1">
            <div class="testBox">
              <p style="text-align: left">{{stepInfo.description}}</p>
              <p style="text-align: left">{{stepInfo.required}}</p>
              <div v-show="stepInfo.status === 2" id="myChart" style="margin:auto;width: 300px;height: 300px;" ></div>
              <span v-show="stepInfo.status === 2" style="font-size: 20px">当前排名：{{orderNum}}</span>
            </div>
            <div class="mt-3 text-center" v-show="stepInfo.status === 1">
              <el-button type="primary" @click="answerPage">调查评测</el-button>
            </div>
            <div class="mt-3 text-center" v-show="stepInfo.status === -1">
              <el-button type="primary" disabled="disabled">暂未开始</el-button>
            </div>
          </div>
          <div class="left col-lg-5" style="width: 50%" v-if="stepInfo && stepInfo.category === 2">
            <div>
              <i class="iconfont iconrenwumingcheng mr-2"></i>
              <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">任务名称：</span>
              <span>编码与调试</span>
            </div>
            <div class="mt-3">
              <i class="iconfont iconrenwumiaoshu mr-2"></i>
              <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">任务描述：</span>
              <p class="ml-4">编码人员依据《详细设计报告》的内容，遵循编码规范的要求对设计元素进行编码实现和调试</p>
            </div>
            <div class="mt-3">
              <i class="iconfont iconrenwuyaoqiu mr-2"></i>
              <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">任务要求：</span>
              <!--                    <p class="ml-4">按照详细设计要求完成编码、调试工作。调试代码中的调试代码应在调试后删除</p>-->
              <p class="ml-4"> {{stepInfo.description}}</p>
            </div>
            <div class="mt-3">
              <i class="iconfont iconshipinjiaocheng mr-2"></i>
              <span style="font-family: inherit;font-weight: 800;line-height: 1.1;color: inherit;">视频教程：</span>
              <h5>编程联系</h5>
              <ul style="list-style-type:decimal;">
                <li style="color:green;"><span>全局样式</span><i class="el-icon-circle-check ml-3"></i></li>
                <li style="color:green;"><span>强调内容</span><i class="el-icon-circle-check ml-3"></i></li>
                <li><span>无序列表、有序列表</span><i class="el-icon-circle-check ml-3"></i></li>
                <li><span>弹出框--弹出框增加过渡动画效果</span><i class="el-icon-circle-check ml-3"></i></li>
              </ul>
            </div>
            <div class="mt-3 text-center">
              <el-button type="primary">去编程</el-button>
            </div>
          </div>
          <div class="left col-lg-1 col-sm-1 col-md-1" style="width: 10%;text-align: center">
            <img style="height: 60px;width: 60px;" src="../../assets/img/top.jpg">
            <p style="font-size: 40px;color: #95aac9">{{no}}</p>
            <p style="font-size: 10px;color: #6715CE">已击败{{noFail}}人</p>
          </div>
          <div class="left col-lg-1 col-sm-1 col-md-1" style="width: 10%;text-align: center;margin-top: 115px;padding-right: 0px">
           <a title="联系助教" :href="href" style="font-size: 10px;color: red;"><i class="iconfont iconzaixiankefu mr-2" style="width: 10px;height: 10px"></i>联系助教</a>
          </div>
        </div>
      </div>
    </div>
    <!--  左侧点评专区-->
    <div class="container1" style="margin-left: 15px;margin-right: 5px;" :class="showEditor===true?'col-md-8':'col-md-5'">
      <div style="height: 600px;">
        <div class="container-fluid box">
<!--          <div class="output box-header with-border">成果物及点评</div>-->
          <h4 class="xm-left-column-label"><span></span>成果物及点评</h4>
          <div class="commentAll" v-if="stepInfo">
            <!--上传成果物区域 begin-->
            <div class="reviewArea clearfix" :style="fileList.length!== 0 ? 'height:0px':''"
                 v-show="stepInfo.status === 1">
              <el-upload
                class="upload-demo"
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-change="handleChange"
                :limit="1"
                :before-remove="beforeRemove"
                :before-upload="beforeAvatarUpload"
                :on-exceed="handleExceed"
                :file-list="fileList">
                <el-button :disabled="fileList.length!== 0 ? true:false" size="small" type="primary" style="float: right;margin-right: -610px;margin-top: -50px;">选择文件
                </el-button>
                <!--              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>-->
              </el-upload>
              <!--            <textarea class="content comment-input" placeholder="Please enter a comment&hellip;" onkeyup="keyUP(this)"></textarea>-->
              <a @click="upLoad" class="plBtn" v-if="fileList.length!== 0">上传成果物</a>
            </div>
            <div class="mt-15 xzwj" v-show="stepInfo.status === 2 || stepInfo.status === 3">
              <el-button type="primary" @click="seeRightAnswer(stepInfo.answer)">查看优秀答案</el-button>
            </div>
            <div class="mt-15 xzwj" v-show="stepInfo.status === -1">
              <el-button type="primary" disabled="disabled">阶段未开始</el-button>
            </div>
            <!--上传成果物区域 end-->
            <!--点评区域 begin-->
            <div class="comment-show">
              <div class="comment-show-con clearfix">
                <div class="comment-show-con-img pull-left"></div>
                <!--              <div class="comment-show-con-img pull-left"><img src="images/header-img-comment_03.png" alt=""></div>-->
                <div class="comment-show-con-list pull-left clearfix" v-show="isReviewData==true" v-for="obj in commentData" v-bind:key="obj.outputId">
                  <div class="commentItem" style="overflow: auto;overflow-x: hidden;height: 500px;">
                    <div class="pl-text clearfix" style="padding-top: 10px;">
                      <span class="my-pl-con" style="font-size: 16px;color: #6612CF;"> 上传成果物名称: </span>
                      <a class="comment-size-name" style="font-size: 16px" @click="downloadTemplate(obj.name)">{{obj.name.split('$_$')[1]}}</a>
                      <span class="date-dz-left pull-right comment-time">{{obj.uploadDate}}</span>
                    </div>
                    <div class="date-dz">
                      <div class="date-dz-right pull-right comment-pl-block">
                        <!--                    <a href="javascript:;" class="removeBlock">删除</a>-->
                        <!--                    <span class="pull-left date-dz-line">|</span>-->
                        <!--                    <a href="javascript:;" class="date-dz-z pull-left"><i class="date-dz-z-click-red"></i>赞 (<i class="z-num">666</i>)</a>-->
                      </div>
                    </div>
<!--                    <div class="hf-list-con" v-show="obj.status!==0">-->
                    <div class="hf-list-con">
                      <div class="all-pl-con">
                        <div class="pl-text hfpl-text clearfix">
                          <a href="#" v-show="obj.commentCode=='REPEAT_CHECK'" class="comment-size-name"> 文档查重 : </a>
                          <a href="#" v-show="obj.commentCode=='CODE_STYLE'" class="comment-size-name"> 代码规范检查 : </a>
                          <a href="#" v-show="obj.commentCode=='DOC_FORMAT'" class="comment-size-name"> 文档格式检查 : </a>
                          <a href="#" v-show="obj.commentCode=='ASSISTANT_COMMENT'" class="comment-size-name"> 助教点评 : </a>
                          <span style="color: #02C365" class="my-pl-con" v-show="obj.status==1">已通过</span>
                          <span style="color: rgba(255,76,85,1)" class="my-pl-con" v-show="obj.status==-1">未通过</span>
                          <span style="color: #FF9600" class="my-pl-con" v-show="obj.status==0">未点评</span>
                          <el-button type="text" class="date-dz-pl pl-hf hf-con-block pull-right" @click="showDetails(obj)" v-show="obj.status!=0" style="line-height: 0;">查看详情</el-button>
<!--                          <a class="date-dz-pl pl-hf hf-con-block pull-right" @click="showDetails(obj.outputId)" v-show="obj.status!=0">查看详情</a>-->
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div v-show="commentData.length === 0" style="text-align: center"><span>暂无上传的成果物</span></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  右侧评论-->
    <div class="container1" id="commentAdd" :class="showEditor===true?'col-md-10 col-md-offset-1':'col-md-3'" :style="showEditor===true?'':'margin-left: 5px'">
      <div style="min-height: 600px;">
        <div class="container-fluid">
          <h4 class="xm-left-column-label"><span></span>问答&讨论<a :title="messageLage" style="cursor: pointer"><i :class="showEditor===true?'el-icon-remove':'el-icon-circle-plus'" style="margin-left: 20px;color: #6612cf"  @click="showEditorComment"></i></a></h4>
          <a style="font-size: 14px;color: rgb(102, 18, 207);margin-top: -35px;float: right;" :href="groupLinks">查看项目的更多评论！</a>
<!--          <div class="output">评论<a style="font-size: 14px;color: #6612CF;margin-left: 78px;">查看项目的更多评论！</a></div>-->
          <div class="commentAll" style="margin: 10px;padding: 10px">
            <p v-if="comment.length==0">暂无评论，我来发表第一篇评论！</p>
            <div v-else class="commentContent">
              <div class="comment" v-for="(item,index) in comment" v-bind:key="index">
                <div class="comment">
                  <b v-if="item.commentId === null || item.commentId === ''">{{item.discussantName}}<span>{{item.commentTime}}</span></b>
                  <span v-if="item.stepName" style="color: #6612cf">#{{item.stepName}}</span>
                  <span v-if="item.materialName" style="color: #6612cf">#{{item.materialName}}</span>
                  <p v-if="item.commentId === null || item.commentId === ''" v-html="item.content" @click="changeCommenter(item.id, item.discussantName)">
                    {{item.content}}
                  </p>
                <list :comment="item" @changeCommenter='changeCommenter'></list>
                </div>
              </div>
            </div>
            <!--回复区域 end-->
          </div>
          <div style="text-align: right">
            <textarea v-show="!showEditor" class="content comment-input" v-model="commentAddData" :placeholder="place"></textarea>
<!--            <a v-show="!showEditor" style="font-size: 14px;color: rgb(102, 18, 207);" @click="showEditorComment">发表更多评论！</a>-->
            <div style="float: left;" v-show="showEditor && place!== '请输入要发表的内容'">{{place}}</div>
            <Editor v-show="showEditor" v-model="article.content"/>
            <el-button type="primary" @click="addCommentArea" :style="showEditor===true?'margin-top: 65px':'margin-top: 5px'">发表</el-button>
          </div>
        </div>
      </div>
    </div>
<!--    弹窗显示机器点评信息-->
    <div class="showCheck" v-show="showCheck">
      <i class="el-icon-error close-btn" style="cursor: pointer"  @click="close"></i>
      <div style="margin-top: 200px;font-size: 20px;color: cornsilk;">
<!--        <div style="font-size: 20px;color: white">错误总数： <span style="color: red">100</span></div>-->
<!--        <div style="font-size: 20px;color: white">类路径： <span style="color: red">A.java</span></div>-->
        <div v-show="machine">{{machine}}结果</div>
<!--        <div>文档格式检查结果</div>-->
      </div>
      <el-table
        :data="tableData"
        stripe
        border
        height="500"
        show-header
        highlight-current-row
        style="width: 60%;margin: 20%;text-align: center;margin-top: 20px;border-radius:5px;" :header-cell-style="{'border-bottom':'solid 1px #d2d3da','color':'white','background-color':'#999'}">
        <el-table-column
          type="index"
          align='center'
          width="50">
        </el-table-column>
        <el-table-column
          prop="fileName"
          label="文件"
          align='center'
          width="350">
        </el-table-column>
        <el-table-column
          prop="error"
          label="错误"
          align='center'>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import Header from '../../../../components/school-training/Header.vue'
import HorizontalTap from '../../../../components/tap/HorizontalTap.vue'
import TeamStepList from '../../../../components/school-training/TeamStepList.vue'
import Editor from '../../../../components/UEditor/tinymce.vue'
import List from '../../../../components/form/List.vue'
import { Loading } from 'element-ui'
import Vue from 'vue'
export default {
  name: 'app',
  components: {
    Header,
    HorizontalTap,
    Editor,
    TeamStepList,
    List
  },
  mounted () {
    this.initEvent()
    this.initData()
  },
  data () {
    return {
      doUpload: '/api/up/file',
      pppss: {
        srid: ''
      },
      winWidth: window.innerWidth,
      data2: [{
        name: '执行任务',
        url: '',
        type: '_self'
      }, {
        name: '任务单',
        url: 'work-order.html',
        type: '_self'
      }, {
        name: '日报任务',
        url: 'daily-tasks.html',
        type: '_self'
      }, {
        name: '相关资料',
        url: 'learning-library.html',
        type: '_self'
      }, {
        name: '成长轨迹',
        url: 'growth-locus.html',
        type: '_self'
      }],
      data3: [{ name: '成果物点评' }],
      currentStageId: -1,
      currentStepId: -1,
      taskData: [],
      stepData: null,
      commentContent: '',
      isShow: false,
      showTapName: '成果物点评',
      isReviewData: true,
      state: 1,
      appointListData: [],
      isTeamLeader: 0,
      memberStatusData: [],
      stepInfo: null,
      teamId: '',
      message: '',
      evaluates: '',
      commentData: [],
      files: {},
      fileName: '暂未上传文件',
      answerShow: false,
      paperInfo: {},
      paperId: '',
      starttime: '',
      answer: {},
      answerLog: '',
      problemidLog: '',
      timeLimit: 0,
      d: '',
      h: '',
      m: '',
      s: '',
      document: '',
      listTotal: [],
      listResult: [],
      orderNum: '暂无排名',
      templateName: [],
      show: true,
      uploadprogress: 0,
      contEnclosure: {},
      fileList: [],
      comment: [],
      commentAddData: '',
      article: {
        content: ''
      },
      showEditor: false,
      href: '',
      commentId: null,
      place: '请输入要发表的内容',
      messageLage: '放大，使用富文本进行发表',
      no: '',
      noFail: '',
      projectName: '',
      groupLinks: '',
      tableData: [],
      showCheck: false,
      machine: '',
      noticeIs: false
    }
  },
  methods: {
    initEvent: function () {
      let me = this
      if (localStorage.getItem('unreadStatus') === 'false') {
        me.noticeIs = false
      } else {
        me.noticeIs = true
      }
      me.winWidth = $('body').width()
      window.onresize = function () {
        me.winWidth = $('body').width()
      }
    },
    initData: function () {
      this.teamId = localStorage.getItem('teamId')
      if (this.teamId === '' || this.teamId === 'undefined' || this.teamId === null) {
        window.location = 'school-main.html'
      } else {
        this.loadStepListData()
      }
    },
    loadStepListData () {
      let me = this
      this.$http.post('/doproject/steplist', {
        'teamid': me.teamId
      }, res => {
        if (res.success) {
          if (res.data.assistantQQ) {
            me.href = 'tencent://message/?Menu=yes&uin=' + res.data.assistantQQ + '&Site=1111&Service=300&sigT=45a1e5847943b64c6ff3990f8a9e644d2b31356cb0b4ac6b24663a3c8dd0f8aa12a595b1714f9d45'
          }
          me.projectName = res.data.projectName
          me.groupLinks = res.data.groupLinks
          for (let obj1 of res.data.stages) {
            obj1.iconShow = false
            obj1.msgStatus = 1
            let k = 0
            for (let obj2 of obj1.steps) {
              if (obj2.status === 2) {
                k++
              }
              if (obj2.msgStatus === 0) {
                obj1.msgStatus = 1
              }
              if (obj2.status === 3 || k === obj1.steps.length) {
                obj1.iconShow = true
                break
              }
            }
          }
          me.stepData = res.data.stages
          me.currentStageId = res.data.currentStageId
          for (let obj of me.stepData) {
            if (obj.stageId === me.currentStageId) {
              me.currentStepId = obj.steps[0].stepId
            }
          }
          this.loadStepinfo()
          this.loadDiscussionTreeInfo()
          // this.loadAppointListData()
          // this.loadMemberStatusData()
          this.getComment()
          this.getNo()
        }
      })
    },
    loadAppointListData () {
      let me = this
      this.$http.post('/doproject/appointstatus', {
        'teamid': me.teamId,
        'stepid': me.currentStepId
      }, res => {
        if (res.success) {
          me.appointListData = res.data.status
          me.isTeamLeader = res.data.teamLeader
        }
      })
    },
    loadDiscussionTreeInfo () {
      let me = this
      this.$http.post('/doproject/discussionTreeInfo', {
        'projectId': localStorage.getItem('projectId')
      }, res => {
        console.log(res)
        if (res.success) {
          me.comment = res.data
          console.log(me.comment)
        }
      })
    },
    loadMemberStatusData () {
      let me = this
      this.$http.post('/doproject/memberstatus', {
        'teamid': me.teamId,
        'stepid': me.currentStepId
      }, res => {
        if (res.success) {
          me.memberStatusData = res.data
        }
      })
    },
    loadStepinfo () {
      let me = this
      this.$http.post('/doproject/stepinfo', {
        'teamid': me.teamId,
        'stepid': me.currentStepId
      }, res => {
        if (res.success) {
          me.stepInfo = res.data
          me.data3 = [{ name: '成果物点评' }]
          this.templateName = []
          if (res.data.templateName.indexOf(',') !== -1) {
            this.templateName = res.data.templateName.split(',')
          } else {
            this.templateName.push(res.data.templateName)
          }
          if (me.stepInfo.category === 1 && me.stepInfo.status === 2) {
            me.echartShow()
          }
          // if (me.stepInfo.type === 1) {
          //   me.data3 = [{ name: '成果物点评' }, { name: '指派任务' }, { name: '队友进行状态' }]
          // } else {
          //   me.data3 = [{ name: '成果物点评' }, { name: '队友进行状态' }]
          // }
        }
      })
    },
    getComment () {
      let me = this
      this.$http.post('/doproject/outputlist', {
        'teamid': me.teamId,
        'stepid': me.currentStepId
      }, res => {
        if (res.data !== null) {
          me.isReviewData = true
          for (let obj of res.data) {
            obj.commentDetailedIsShow = false
            obj.message = ''
          }
          me.commentData = res.data
        } else {
          me.isReviewData = false
          me.commentData = []
        }
        console.log(me.commentData)
      })
    },
    getNo () {
      let me = this
      this.$http.post('/doproject/rankStatisticsInfo', {
        'teamId': me.teamId
      }, res => {
        console.log(res)
        me.no = res.data.classRank
        me.noFail = res.data.classHeadCount - res.data.classRank
      })
