        //     }
        //   })
        me.$http.post('/doproject/submitfeedback', {
          'outputid': outputid,
          'feedbacks': me.commentContent
        }, res => {
          if (res.success) {
            me.$message('保存成功')
          }
        })
        me.upLoad()
      }
    },
    playVideo (video) {
      window.open('' + video, '_blank')
    },
    downloadTemplate (name) {
      const loading = Loading.service({
        lock: true,
        text: '下载中请稍候...',
        spinner: 'el-icon-loading',
        // background: 'rgba(0, 0, 0, 0.7)'
        background: 'rgba(f, 0, 0, 0.7)'
      })
      this.$axios({
        method: 'post',
        url: 'http://39.100.37.131:8085/lying-web/doproject/output/downloadtemplate',
        data: { 'filename': name },
        responseType: 'blob',
        headers: {
          token: localStorage.getItem('token'),
          'Content-Type': 'application/json; charset=UTF-8'
        }
      }).then(
        (response) => {
          var blob = new Blob([response.data])
          var downloadElement = document.createElement('a')
          var href = window.URL.createObjectURL(blob)
          downloadElement.href = href
          downloadElement.download = name.split('$_$')[1]
          document.body.appendChild(downloadElement)
          downloadElement.click()
          document.body.removeChild(downloadElement)
          window.URL.revokeObjectURL(href)
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      ).catch(
        (error) => {
          console.log(error)
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      )
    },
    seeRightAnswer (outputName) {
      let me = this
      if (outputName !== null) {
        me.$hideContent()
        me.$http.post('/assistant/openpdf', { outputName: outputName }, res => {
          if (res.success) {
            res.data = JSON.parse(res.data)
            console.log(res.data.fileUrl)
            me.$showPDF(res.data.fileUrl)
            // me.pdfUrl = res.data.fileUrl
          }
        })
      } else {
        me.$message('此任务无答案')
      }
    },
    answerPage () {
      let me = this
      me.$http.post('/doproject/evaluating/paperinfo', {
        'teamid': me.teamId,
        'stepid': me.currentStepId
      }, res => {
        if (res.success) {
          for (let obj of res.data.problems) {
            if (obj.type === 0) {
              obj.radio = ''
            } else if (obj.type === 1) {
              obj.radio = []
            }
            Vue.set(me.answer, obj.problemId, '')
          }
          me.timeLimit = res.data.timeLimit * 1000 * 60
          this.countTime()
          me.paperInfo = res.data
          me.paperId = res.data.paperId
        }
      })
      let year = new Date(new Date()).getFullYear()
      let month = new Date(new Date()).getMonth() + 1 < 10 ? '0' + (new Date(new Date()).getMonth() + 1) : new Date(new Date()).getMonth() + 1
      let date = new Date(new Date()).getDate() < 10 ? '0' + new Date(new Date()).getDate() : new Date(new Date()).getDate()
      let hh = new Date(new Date()).getHours() < 10 ? '0' + new Date(new Date()).getHours() : new Date(new Date()).getHours()
      let mm = new Date(new Date()).getMinutes() < 10 ? '0' + new Date(new Date()).getMinutes() : new Date(new Date()).getMinutes()
      let ss = new Date(new Date()).getSeconds() < 10 ? '0' + new Date(new Date()).getSeconds() : new Date(new Date()).getSeconds()
      me.starttime = year + '-' + month + '-' + date + ' ' + hh + ':' + mm + ':' + ss
      me.answerShow = true
    },
    commitPage () {
      let me = this
      for (let obj of me.paperInfo.problems) {
        if (obj.type === 0) {
          // 单选添加答案
          me.answer[obj.problemId] = obj.radio
        } else if (obj.type === 1) {
          // 多选题时将obj.radio转化为以逗号分隔的字符串
          obj.radio = obj.radio.sort()
          var answers = ''
          for (var i = 0; i < obj.radio.length; i++) {
            if (i === 0) {
              answers = obj.radio[i]
            } else {
              answers += ',' + obj.radio[i]
            }
          }
          obj.radio = answers
          me.answer[obj.problemId] = obj.radio
        }
      }
      me.$http.post('/doproject/evaluating/submit', {
        'teamid': me.teamId,
        'stepid': me.currentStepId,
        'paperid': me.paperId,
        'starttime': me.starttime,
        'answer': me.answer
      }, res => {
        if (res.success) {
          me.$message('保存成功')
          me.loadStepListData()
          me.answerShow = false
        }
      })
    },
    // 暂时弃用
    // answerOptionSet (key, answerOption, questionType) {
    //   let me = this
    //   for (let obj of me.paperInfo.problems) {
    //     if (questionType === 0) {
    //       // 单选添加答案
    //       if (obj.problemId === key) {
    //         obj.radio = answerOption
    //         Vue.set(me.answer, key, answerOption)
    //       }
    //     } else if (questionType === 1) {
    //       if (obj.problemId === key) {
    //         // console.log(obj.radio)
    //         if (obj.radio.indexOf(answerOption) === -1) {
    //           obj.radio.push(answerOption)
    //         }
    //         obj.radio = obj.radio.sort()
    //         console.log(obj.radio)
    //         var answers = ''
    //         for (var i = 0; i < obj.radio.length; i++) {
    //           answers += obj.radio[i]
    //         }
    //         // if (obj.radio.indexOf('A') !== -1) {
    //         //   answers = 'A'
    //         // } else if (obj.radio.indexOf('B') !== -1) {
    //         //   if ()
    //         // }
    //         console.log(answers)
    //         Vue.set(me.answer, key, answers)
    //         // if (obj.radio === '') {
    //         //   obj.radio = answerOption
    //         // } else if (obj.radio !== '') {
    //         //   // let radios = obj.radio.split(',')
    //         //   if (answerOption.indexOf(obj.radio) === -1) {
    //         //     alert('123')
    //         //   } else {
    //         //     // 答案中无此选项
    //         //     obj.radio = obj.radio + ',' + answerOption
    //         //   }
    //         // }
    //       }
    //     }
    //   }
    //
    //   // 记录测评日志
    //   // if (me.problemidLog === key && me.answerLog === answerOption) {
    //   //   me.$message('请勿重复点击')
    //   // } else {
    //   //   me.$http.post('/doproject/evaluating/logsubmit', {
    //   //     'problemid': key,
    //   //     'answer': answerOption
    //   //   }, res => {
    //   //     if (res.success) {
    //   //       me.$message('记录日志成功')
    //   //       me.problemidLog = key
    //   //       me.answerLog = answerOption
    //   //     }
    //   //   })
    //   // }
    // },
    countTime: function () {
      if (this.timeLimit >= 0) {
        this.d = Math.floor(this.timeLimit / 1000 / 60 / 60 / 24)
        this.h = Math.floor(this.timeLimit / 1000 / 60 / 60 % 24)
        this.m = Math.floor(this.timeLimit / 1000 / 60 % 60)
        this.s = Math.floor(this.timeLimit / 1000 % 60)
      }
      if (this.timeLimit === 0) {
        this.commitPage()
        this.$message('时间已到')
      }
      this.timeLimit = this.timeLimit - 1000
      setTimeout(this.countTime, 1000)
    },
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      var myChart = this.$echarts.init(this.document)
      // 绘制图表
      myChart.setOption({
        title: {
          text: '测评结果',
          x: 'left'
        },
        tooltip: {},
        radar: {
          name: {
            textStyle: {
              color: '#fff',
              backgroundColor: '#999',
              borderRadius: 3,
              padding: [3, 5]
            }
          },
          indicator: this.listTotal
        },
        series: [{
          name: '各个维度题目正确数',
          type: 'radar',
          data: this.listResult
        }]
      })
    },
    //  删除成果物
    deleteComment (outputName) {
      let me = this
      me.$http.post('/doproject/output/deleteOutput', {
        outputName: outputName
      }, res => {
        if (res.success) {
          // me.$message('删除成功')
          me.loadStepListData()
        }
      })
    },
    echartShow () {
      let me = this
      me.$http.post('/doproject/evaluating/evaluatingresultinfo', {
        'teamid': me.teamId,
        'stepid': me.currentStepId
      }, res => {
        me.orderNum = res.data.order
        me.listResult = res.data.list2
        me.listTotal = res.data.list1
        me.$nextTick(function () {
          me.document = document.getElementById('myChart')
          me.drawLine()// => 'updated'
        })
      })
    },
    // 文件格式检查  20190711
    beforeAvatarUpload (file) {
      this.file = file
      this.files = file
      const extension = file.name.split('.')[1] === 'doc'
      const extension1 = file.name.split('.')[1] === 'docx'
      const extension2 = file.name.split('.')[1] === 'zip'
      const isLt2M = file.size / 1024 / 1024 < 20
      if (!extension && !extension1 && !extension2) {
        this.$message.error('上传模板只能是 doc、docx、zip格式!')
        return false
      }
      if (!isLt2M) {
        this.$message.error('上传模板大小不能超过 20MB!')
        return false
      }

      this.fileName = file.name
      return true // 返回false不会自动上传
    },
    // 上传文件  20190711
    handleChange (file, fileList) {
      this.fileList = fileList.slice(-3)
    },
    handleExceed (files, fileList) {
      this.$message.warning(`当前限制选择 1 个文件，已经上传一个文件，如需修改，请删除已上传的文件后再重新上传`)
    },
    beforeRemove () {
      this.fileList = []
    },
    // 上传成果物 20190711
    upLoad () {
      let me = this
      var formData = new FormData()
      formData.append('file', me.file)
      formData.append('teamid', me.teamId)
      formData.append('stepid', me.currentStepId)
      this.$http.uploadFile('/doproject/output/upload', formData, res => {
        this.fileList = []
        if (res.success) {
          me.$message('上传成果物成功')
          me.loadStepListData()
        }
      })
    },
    //  针对进行评论
    changeCommenter (id, name) {
      this.commentId = id
      console.log(name)
      this.place = '回复：' + name
    },
    //  提交评论
    addCommentArea () {
      var addcontent = ''
      if (this.showEditor === false) {
        console.log(this.commentAddData)
        addcontent = this.commentAddData
      } else {
        console.log(this.article.content)
        addcontent = this.article.content
      }
      // 获取当前时间
      let date = new Date()
      var Y = date.getFullYear() + '-'
      var M = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) + '-' : date.getMonth() + 1 + '-'
      var D = date.getDate() < 10 ? '0' + date.getDate() + ' ' : date.getDate() + ' '
      var h = date.getHours() < 10 ? '0' + date.getHours() + ':' : date.getHours() + ':'
      var m = date.getMinutes() < 10 ? '0' + date.getMinutes() + ':' : date.getMinutes() + ':'
      var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      let me = this
      console.log(me.currentStepId)
      me.$http.post('/doproject/submitDiscussion', {
        'commentTime': Y + M + D + h + m + s,
        'content': addcontent,
        'projectId': localStorage.getItem('projectId'),
        'commentId': me.commentId,
        'stepId': me.currentStepId
      }, res => {
        console.log(res)
        me.$message('发布成功')
        me.loadDiscussionTreeInfo()
        me.commentAddData = ''
        me.article.content = ''
        me.commentId = null
        me.place = '请输入要发表的内容'
      })
    },
    //  显示editor
    showEditorComment () {
      this.showEditor = !this.showEditor
      if (this.showEditor === false) {
        this.messageLage = '放大，使用富文本进行发表'
      } else {
        this.messageLage = '缩小，使用普通文本框进行发表'
      }
      this.$nextTick(() => {
        document.querySelector('#commentAdd').scrollIntoView(true)
      })
    },
    // 显示点评详情
    showDetails (obj) {
      console.log(obj.outputId)
      let str = ''
      if (obj.commentCode === 'ASSISTANT_COMMENT') {
        this.$http.post('/doproject/assistantcommentinfo', {
          'outputid': obj.outputId
        }, res => {
          if (res.success) {
            console.log(res)
            str = ' <div class="doc-detail">\n' + '<div style="color: #6715CE">成果物缺陷点：</div>\n' + '<span>\n'
            for (let obj of res.data.advices) {
              str += obj + ' ; '
            }
            str += '</span>\n' + '<div style="color: #6715CE">助教点评：</div>\n' + '<span>\n'
            for (let obj1 of res.data.evaluates) {
              str += obj1 + ' ; '
            }
            str += '</span>\n' + '</div>'
            this.$alert(str, '点评内容', {
              dangerouslyUseHTMLString: true
            })
          }
        })
      } else {
        this.tableData = []
        this.$http.post('/doproject/systemCommentInfo', {
          'outputId': obj.outputId,
          'commentCode': obj.commentCode
        }, res => {
          if (res.success) {
            for (let obj of res.data.codeStyles) {
              for (let obj1 of obj.errors) {
                this.tableData.push({
                  fileName: obj.fileName,
                  error: obj1
                })
              }
            }
          }
        })
        if (obj.commentCode === 'CODE_STYLE') {
          this.machine = obj.name.split('$_$')[1] + '代码规范检查'
        } else {
          if (obj.commentCode === 'DOC_FORMAT') {
            this.machine = obj.name.split('$_$')[1] + '文档格式检查'
          } else {
            this.machine = obj.name.split('$_$')[1] + '文档查重'
          }
        }
        this.showCheck = true
      }
    },
    close () {
      this.showCheck = false
    }
  }
}
